:root {
  --first-color: #fff;
  --second-text-color: #ff6b01;
  --third-text-color: #000;
  --fourth-text-color: #86515d;
}

body {
  letter-spacing: .03em;
  background-color: #0ff;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 1.6;
}

body *, body :before, body :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.hide-title {
  clip: rect(0 0 0 0);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

